<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="领料人员">
          <el-select
            v-model="listQuery.materials_person"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in PersonCodeNameList"
              :key="index + ' dialog'"
              :label="item.user_name"
              :value="item.user_code"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增 弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="领料人员" prop="materials_person">
                <el-select
                  v-model="dialogForm.materials_person"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option
                    v-for="(item, index) in PersonCodeNameList"
                    :key="index + ' dialog'"
                    :label="item.user_name"
                    :value="item.user_code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="领料时间" prop="material_time">
                <el-date-picker
                  v-model="dialogForm.material_time"
                  type="datetime"
                  style="width: 100%"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  format="yyyy-MM-dd HH:mm:ss"
                  placeholder="选择日期"
                >
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="备注" prop="description">
            <el-input
              type="textarea"
              v-model="dialogForm.description"
            ></el-input>
          </el-form-item>
          <p class="dialog-title">物料明细</p>
          <el-button type="primary" size="small" plain @click="AddListRow()"
            >添加物料</el-button
          >
          <el-table
            :data="dialogForm.response_datas"
            style="width: 100%"
            class="dialogTable"
            height="240"
          >
            <el-table-column fixed="left" label="物料名称" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_code'"
                >
                  <el-select
                    v-model="scope.row.matter_code"
                    placeholder="请选择"
                    style="width: 100%"
                    @change="dialogMatterCodeChange(scope.$index, scope.row)"
                  >
                    <el-option
                      v-for="(item, index) in NoPagePersonMatterData"
                      :key="index + ' search'"
                      :label="item.matter_name"
                      :value="item.matter_code"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="规格型号" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.rule'"
                >
                  <span>{{ scope.row.rule }}</span>
                  <!-- <el-input v-model="scope.row.rule" readonly></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="类型" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_category'"
                >
                  <span>{{ scope.row.matter_category }}</span>
                  <!-- <el-input v-model="scope.row.matter_category"></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="库存数量" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_usage'"
                >
                  <span>{{ scope.row.matter_usage }}</span>
                  <!-- <el-input v-model.number="scope.row.matter_usage"></el-input> -->
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="领料数量" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_count'"
                  :rules="[
                    {
                      required: true,
                      message: '数量不能为空',
                      trigger: 'blur',
                    },
                    {
                      type: 'number',
                      message: '数量必须为数值',
                      trigger: 'blur',
                    },
                    {
                      type: 'number',
                      min: 0,
                      max: scope.row.matter_usage,
                      message: '数量范围0-' + scope.row.matter_usage,
                      trigger: 'blur',
                    },
                  ]"
                >
                  <el-input v-model.number="scope.row.matter_count"></el-input>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="操作" width="80" align="center">
              <template slot-scope="scope">
                <el-form-item>
                  <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(scope.$index, scope.row)"
                    >删除</el-button
                  >
                </el-form-item>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MaterialRequisition",
  components: {
    ELTable,
  },

  data() {
    return {
      // 筛选
      listQuery: {
        product_plan_code: "",
        materials_person: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        product_plan_code: "",
        materials_person: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增弹出层
      dialogTitle: "",
      dialogFlag: false,

      dialogForm: {
        materials_production_code: "",
        materials_person: "",
        product_plan_code: "",
        material_time: "",
        description: "",
        response_datas: [
          {
            matter_code: "",
            rule: "--",
            matter_category: "--",
            matter_usage: "--",
            matter_count: "",
          },
        ],
      },
      dialogFormBase: {
        materials_production_code: "",
        materials_person: "",
        product_plan_code: "",
        material_time: "",
        description: "",
        response_datas: [
          {
            matter_code: "",
            rule: "--",
            matter_category: "--",
            matter_usage: "--",
            matter_count: "",
          },
        ],
      },
      dialogFormRules: {
        // product_plan_code: [
        //   { required: true, message: "请选择生产计划", trigger: "change" },
        // ],
        materials_person: [
          { required: true, message: "请选择领料人员", trigger: "change" },
        ],
        material_time: [
          { required: true, message: "请选择领料时间", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        // {
        //   label: "生产计划",
        //   field: "product_plan_name",
        //   align: "center",
        // },
        {
          label: "领料人员",
          field: "materials_person_name",
          align: "center",
        },
        {
          label: "领料时间",
          field: "material_time",
          align: "center",
        },
        {
          label: "备注",
          field: "description",
          align: "center",
        },

        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  computed: {
    ...mapState(["PersonCodeNameList", "NoPagePersonMatterData"]),
  },
  methods: {
    ...mapActions([
      "getLogisticPersonCodeName",
      "getLogisticNoPagePersonMatter",
    ]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticProductPickMatter(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticProductPickMatter(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticProductPickMatter(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.dialogMaterialListListQuery.page = 1;
      this.dialogMaterialListListQuery.page_size = val;
      this.getLogisticProductPickMatter(this.listQuery);
    },

    // 生产领料列表 查询
    async getLogisticProductPickMatter(params) {
      baseManagement.getLogisticProductPickMatter(params).then((res) => {
        console.log("生产领料列表 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;

          tableData.forEach((tableDataItem) => {
            this.PersonCodeNameList.forEach((PersonCodeNameListItem) => {
              if (
                tableDataItem.materials_person ==
                PersonCodeNameListItem.user_code
              ) {
                tableDataItem.materials_person_name =
                  PersonCodeNameListItem.user_name;
              }
            });
          });

          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 生产领料列表 新增
    postLogisticProductPickMatter(params) {
      baseManagement.postLogisticProductPickMatter(params).then((res) => {
        console.log("生产领料列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticProductPickMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    AddListRow() {
      this.dialogForm.response_datas.push({
        matter_code: "",
        rule: "",
        matter_category: "",
        matter_usage: "",
        matter_count: "",
      });
    },
    dialogMatterCodeChange(index, rowData) {
      this.NoPagePersonMatterData.forEach((item, index) => {
        if (rowData.matter_code == item.matter_code) {
          rowData.rule = item.rule;
          rowData.matter_category = item.matter_category;
          rowData.matter_usage = item.matter_count;
        }
      });
    },
    addBtn() {
      this.dialogTitle = "新增生产领料";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 生产领料列表 修改
    async colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改生产领料";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      rowData.response_datas.forEach((rowDataItem) => {
        this.NoPagePersonMatterData.forEach((NoPagePersonMatterDataItem) => {
          if (
            rowDataItem.matter_code == NoPagePersonMatterDataItem.matter_code
          ) {
            rowDataItem.matter_id = NoPagePersonMatterDataItem.matter_id;
            rowDataItem.matter_name = NoPagePersonMatterDataItem.matter_name;
            rowDataItem.rule = NoPagePersonMatterDataItem.rule;
            rowDataItem.matter_category =
              NoPagePersonMatterDataItem.matter_category;
            rowDataItem.matter_usage = NoPagePersonMatterDataItem.matter_count;
          }
        });
      });
      console.log("rowData.response_datas", rowData.response_datas);
      this.dialogForm = {
        materials_production_code: rowData.materials_production_code,
        materials_person: rowData.materials_person,
        product_plan_code: rowData.product_plan_code,
        material_time: rowData.material_time,
        description: rowData.description,
        response_datas: rowData.response_datas,
      };
    },

    // 生产领料列表 修改
    getLogisticPutProductPickMatter(params) {
      baseManagement.getLogisticPutProductPickMatter(params).then((res) => {
        console.log("生产领料列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticProductPickMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      console.log(this.dialogTitle);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增生产领料") {
            console.log("新增生产领料", this.dialogForm);
            this.postLogisticProductPickMatter(this.dialogForm);
          } else if (this.dialogTitle == "修改生产领料") {
            console.log("修改生产领料", this.dialogForm);
            this.dialogForm.response_datas = JSON.stringify(
              this.dialogForm.response_datas
            );
            this.getLogisticPutProductPickMatter(this.dialogForm);
          }
        }
      });
    },

    // 生产领料列表 删除
    getLogisticDeleteProductPickMatter(params) {
      baseManagement.getLogisticDeleteProductPickMatter(params).then((res) => {
        console.log("生产领料列表 删除", res);
        if (res.code == 0) {
          this.getLogisticProductPickMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 点击删除当前行
    handleDelete(index, rowData) {
      if (rowData.materials_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            materials_production_code: rowData.materials_production_code,
            response_datas: JSON.stringify([
              { materials_code: rowData.materials_code },
            ]),
          };
          this.getLogisticDeleteProductPickMatter(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          materials_production_code: rowData.materials_production_code,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteProductPickMatter(params);
      });
    },
  },
  mounted() {
    this.getLogisticPersonCodeName();
    // 不分页的物料详情表接口
    this.getLogisticNoPagePersonMatter();

    // 生产领料列表 查询
    this.getLogisticProductPickMatter(this.listQuery);
  },
};
</script>

<style lang="scss" scoped>
.dialogTable {
  .el-form-item {
    // margin: 0;
    /deep/ .el-form-item__content {
      margin: 0 !important;
    }
  }
}
</style>